import React from "react";

const UpvotesSection = ({ articleName, setarticleInfo, upvotes }) => {
  const upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote`, {
      method: "post"
    });
    const body = await result.json();
    setarticleInfo(body);
  };
  const upvoteArticleClear = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote/clear`, {
      method: "post"
    });
    const body = await result.json();
    setarticleInfo(body);
  };
  return (
    <div className="upvotes-section">
      <button onClick={() => upvoteArticle()}>Add Upvote</button>
      <button onClick={() => upvoteArticleClear()}>Clear Upvote</button>
      <p className="upvotes-margin-text">
        This post has been upvoted {upvotes} times
      </p>
    </div>
  );
};
export default UpvotesSection;
